.works {
  /*grid-area: w;*/
  /*grid-gap: 10px;*/
  /*margin: 10px 10% 10px 10%;*/
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 80vh;
}

.container {
  background-color: rgb(35%, 35%, 35%);
  min-height: 80vh;
  width: 90%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  color: lightgreen;
  border: 1px solid;
}

.wrap {
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
}

.title {
  font-size: 18px;
  margin: 10px;
  color: white;
}

.block {
  display: flex;
  flex-direction: column;
  height: 450px;
  color: green;
  align-items: center;
  justify-content: flex-start;
  margin: 10px;
  background-color: paleturquoise;
}

.buttonClick {
  height: 40px;
  width: 100px;
  background-color: lightgreen;
  color: grey;
  border-radius: 10px;
  font-size: 12pt;
}

.img {
  display: flex;
  height: 320px;
  width: 320px;
  /*border-radius: 50%;*/
  background-color: linen;
  justify-content: center;
  align-items: center;
  /*margin: auto;*/
}

@media screen and (max-width: 1200px) {
  .container {justify-content: center}
}