.footer {
  /*grid-area: f;*/
  /*grid-gap: 10px;*/
  /*margin: 5px 10px 10px 10px;*/
  height: 20vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;

}

.main {
  background-color: rgb(20%, 20%, 20%);
  width: 90%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 16px;
  color: lightgreen;
  height: 100%;
  border: 1px solid;
}

.title {
  margin: 10px;
  font-size: 14px;
  color: white;
}

.wrapper {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
  /*margin: 10px;*/
  /*background-color: darkgray;*/
  border: 1px solid;
  height: 100%;
  width: 360px;
  font-size: 70%;
  /*text-align: center;*/
}

.icon {
  background-color: grey;
  /*border: 1px solid;*/
  border-radius: 5px;
  height: 65px;
  width: 65px;
  display: flex;
  justify-content: center;
  align-items: center;
}

@media screen and (max-width: 500px) {
  .wrapper {width: 100%}
}
@media screen and (max-width: 500px) {
  .icon {height: 50px;
    width: 50px;}
}