.contacts {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 24px;
  height: 30vh;
}

.main {
  background-color: rgb(25%, 25%, 25%);
  width: 90%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 24px;
  color: lightgreen;
  border: 1px solid;
}

.title {
  margin: 10px;
  font-size: 18px;
  color: white;
}

.wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  height: 90%;
  width: 90%;
}

.form {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 5px;
  background-color: darkgray;
  /*border: 1px solid;*/
  height: 30px;
  width: 360px;
}

.buttonClick {
  height: 40px;
  width: 100px;
  background-color: lightgreen;
  color: grey;
  border-radius: 10px;
  font-size: 12pt;
}

@media screen and (max-width: 500px) {
  .wrapper {
    width: 100%}}

@media screen and (max-width: 500px) {
  .form {
    height: 30px;
    width: 250px;}}

